.about_bg{
    background-image: url('../../images/p51.png');
    background-size: 100% 100%;
    background-repeat: no-repeat;
    width: 100%;
    height: 85vh;
    margin-bottom: 3rem;
}
.about_headings h1{
    font-size: 2.5rem;
    text-align: center;
    padding: 7rem 0 1rem 0;
    color: #2D2D2D;
    text-transform: capitalize;
}
.about_headings p{
    font-size: 1.2rem;
    text-align: center;
    color: #2D2D2D;
}
/* 2nd section css here */
.about_box1{
    width: 100%;
    height: 100%;
    background: #FFFFFF 0% 0% no-repeat padding-box;
box-shadow: 0px 25px 25px #00000026;
opacity: 1;
}
.about_box1 h3{
    padding: 1rem 0 .5rem 1rem;
    color: #FC7A7A;
    font-size: 1rem;
    border-left: 2px solid #FC7A7A;
    margin-left: 1rem;
}
.about_box1 h1{
    padding: 2rem 0 .5rem 2rem;
    font-size: 1rem;
    color: #2D2D2D;
    
}
.about_box1 p{
    padding: .5rem 0 4rem 2rem;
    font-size: .9rem;
    color: #2D2D2D;
    
}
.about_box2{
    position: relative;
    width: 100%;
    height: 170%;
    background: #FFFFFF 0% 0% no-repeat padding-box;
box-shadow: 0px 25px 25px #00000026;
opacity: 1;
margin-bottom: 5rem;
}
.about_box2 h3{
    padding: 1rem 0 .5rem 1rem;
    color: #FC7A7A;
    font-size: 1rem;
    border-left: 2px solid #FC7A7A;
    margin-left: 1rem;
}
.about_box2 h1{
    padding: 2rem 0 .5rem 2rem;
    font-size: 1rem;
    color: #2D2D2D;
    
}
.about_box2 p{
    padding: .5rem 0 1rem 2rem;
    font-size: .9rem;
    color: #2D2D2D;
    
}
.our_team_img{
    position: absolute;
    top: 70%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 60%;
    height: 50%;
    /* margin: 1rem; */
    background: #BFBFBF 0% 0% no-repeat padding-box;
opacity: 1;
}
.our_team_img h1{
  font-size: 1rem;
  text-align: center;
  color: yellow;
}
.note_ceo_section{
    margin-top: 18rem;
}
.note_ceo_heading h1{
    font-size: 2rem;
    color: #132646;
    padding: 1rem 0 2rem 0;
}
.note_ceo_heading p{
    font-size: 1rem;
    color: #2D2D2D;
}
.note_ceo_heading h2{
    padding-top: 2rem;
    font-size: 1.5rem;
    color: #2D2D2D;
    text-align: center;
}
.note_ceo_heading h4{
    font-size: 1rem;
    color: #2D2D2D;
    text-align: center;
}
.clients_bold{
    font-family: 'Oswald', sans-serif;
}
@media screen and (max-width:800px){
    .about_bg{
        height: 70vh;
        margin-bottom: 3rem;
    }
    .about_headings h1{
        font-size: 2rem;
        padding: 3rem 0 1rem 0;
        
    }
    .about_headings p{
        font-size: 1rem; 
    } 
    .our_team_img{
        top: 70%;
        left: 50%;
        width: 72%;
        height: 45%;
    }
    .note_ceo_section{
        margin-top: 18rem;
    }
    .note_ceo_heading h1{
        font-size: 1.5rem;
       
        padding: 1rem 0 2rem 1rem;
    }
    .note_ceo_heading p{
        font-size: 1rem;
        padding-left: 1rem;
       
    }
   
}