.blog_section{
    background-image: url('../../images/p41.png');
    background-size: 100% 100%;
    background-repeat: no-repeat;
    height: 75vh;
}
.blog_first_section h1{
    text-align: center;
    padding: 7rem 0 1rem 0;
    font-size: 2rem;
    color: #2D2D2D;
text-transform: uppercase;

}
.blog_first_section p{
    text-align: center;
    font-size: 1.2rem;
    color: #2D2D2D;
}
.box_1_bg{
    position: relative;
    background-image: url('../../images/p42.png');
    background-size: 100% 100%;
    background-repeat: no-repeat;
    height: 60vh;

}
.box_1{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    /* margin: 4rem 0 4rem 0; */
    width: 22rem;
    height: 15rem;
    background: #FFFFFF 0% 0% no-repeat padding-box;
box-shadow: 0px -15px 15px #00000014;
opacity: 1;
}
.box_1 p{
    font-size: .6rem;
    padding: 1rem .5rem 1.5rem .5rem;
}

.pera_bg p{
    margin:1rem 0 0 1rem;
    background: #FCB4CC;
    border-radius: 4px;
    width: 6rem;
    height: .2rem !important;

}
.box_1 h2{
    font-size: 1rem;
    padding: 1rem 1rem 0 1rem;

}
.box_1 h5{
    font-size: .5rem;
    padding: 1rem;

}
.box_2{
    width: 100%;
    height: 60vh;
    background: #FFFECF 0% 0% no-repeat padding-box;
border-radius: 12px;
opacity: 1;
}
.box_2 h1{
    font-size: 1.5rem;
    padding:2rem 1rem 1rem 1rem;
    color: #2D2D2D;
}

.box_2 p{
    line-height:2.5;
    font-size: .8rem;
    padding: 1rem;
    color: #2D2D2D;
}
.blogs_box_heading h1{
    font-size: 1.2rem;
    padding: 1rem;
}
.img_computer{
    width: 95%;
    height: 55%;
}
.bg_yellow{
    width: 95%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background: #FFFC94 0% 0% no-repeat padding-box;
}
.bg_yellow p{
    font-size: .8rem;
    color: #636363;
text-transform: uppercase;
padding: .5rem 1rem 0 1rem;

}
.bg_pink{
    width: 95%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background: #FF7D7D 0% 0% no-repeat padding-box;
}
.bg_pink p{
    font-size: .8rem;
    color: #EEEEEE;
    text-transform: uppercase;
padding: .5rem 1rem 0 1rem;

}
.bg_green{
    width: 95%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background: #32C895 0% 0% no-repeat padding-box;
}
.bg_green p{
    font-size: .7rem;
    color: #EEEEEE;
    text-transform: uppercase;
padding: .5rem 1rem 0 1rem;

}
@media screen and (max-width:800px){
    .blog_section{
        height: 50vh;
        width: 100%;
    }
    .blog_first_section h1{
        padding: 5rem 0 .5rem 0;
        font-size: 1.5rem;
    }
    .blog_first_section p{
        font-size: 1rem;
    }
    .box_1_bg{
        height: 50vh;
        margin: 0 .5rem 0 .5rem;
    
    }
    .box_1{
        height: 12rem;
    }
    .box_2{
        margin-top: 1rem;
        width: 100%;
        height: 50vh;
    }
    .box_2 p{
        line-height:2.5;
    }
    .blogs_box_heading h1{
        font-size: 1.1rem;
        padding: 1rem 1rem 1.5rem 1rem;
    }
}