.footer_bg{
    background: #2b2b2b 0% 0% no-repeat padding-box;
}
.footer_section {
    position: relative;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap;
  margin-top: 1rem;

}
