.footer_left1 h3 {
  font-size: 1.2rem;
  padding: 2rem 0 2rem 6rem;
  color: #7e7e7e;
}
.footer_left2 h3 {
  font-size: 1.2rem;
  padding: 2rem 0 2rem 2rem;
  color: #7e7e7e;
}
.footer_center h3 {
  font-size: 1.2rem;
  padding: 2rem 0 2rem 2rem;
  color: #7e7e7e;
}

.footer_right1 h3{
  font-size: 1.2rem;
  padding: 2rem 0 2rem 2rem;
  color: #7e7e7e;
}
.footer_right2 h3{
  font-size: 1.2rem;
  padding: 2rem 0 2rem 2rem;
  color: #7e7e7e;
}
.footer_left1 p{
    font-size: 0.8rem;
    color: #eeeeee;
    padding: 1rem 0 2rem 6rem;
}
.footer_left2 p{
    font-size: 0.8rem;
    color: #eeeeee;
    padding: 1rem 0 2rem 2rem;
}
.footer_center p{
    font-size: 0.8rem;
    color: #eeeeee;
    padding: 1rem 0 2rem 2rem;
}
.footer_right1 p{
    font-size: 0.8rem;
    color: #eeeeee;
    padding: 1rem 0 2rem 2rem;
}
.footer_right2 p {
  font-size: 0.8rem;
  color: #eeeeee;
  padding: 1rem 0 2rem 2rem;
}
.line1{
    width: 30%;
    color: red;
    border: 2px solid red;
   
}
.footer_imgs{
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
    margin: 2rem 0 0 2rem;
}
.footer_all_imgs img{
    width: 8rem;
    height: 5rem;
    margin: 2rem 2rem 0 2rem;
}
.last_footer{

    background: #142543 0% 0% no-repeat padding-box;
}
.last_footer p{
    color: #EEEEEE;
    text-align: center;
    padding: 4rem ;
}
hr {
    margin-top: 1rem;
    margin-bottom: 0;
    border: 0;
    border-top: 1px solid rgba(0,0,0,.1);
}
@media screen and (max-width:800px){
    .footer_section {
        display: flex;
        flex-direction: row;
        justify-content:center;
        flex-wrap: wrap;
        margin-top: 1rem;
        background: #2b2b2b 0% 0% no-repeat padding-box;
      }
      .footer_left1{
        margin: .5rem;
        width: 10rem; 
      } 
      .footer_left2{
        margin: .5rem;
        width: 5rem;
      } 
      .footer_center{
        margin: .5rem;
        width: 4.5rem;
      } 
      .footer_right1{
        margin:0 .5rem 1rem 0;
        width: 8rem;
      } 
      .footer_right2{
          margin: 0 .5rem 1rem 1.5rem;
          width: 8rem;
      }
    .footer_left1 h3 {
        font-size: 1rem;
        padding: 1rem 0 1rem 1rem;
        color: #7e7e7e;
      }
      .footer_left2 h3 {
        font-size: 1rem;
        padding: 1rem 0 1rem 0;
        color: #7e7e7e;
      }
      .footer_center h3 {
        font-size: 1rem;
        padding: 1rem 0 1rem 0;
        color: #7e7e7e;
      }
      
      .footer_right1 h3{
        font-size: 1rem;
        padding: 1rem 0 1rem 0;
        color: #7e7e7e;
      }
      .footer_right2 h3{
        font-size: 1rem;
        padding: 1rem 0 1rem 0;
        color: #7e7e7e;
      }
      .footer_left1 p{
          font-size: 0.7rem;
          color: #eeeeee;
          padding: 1rem 0 2rem 1rem;
      }
      .footer_left2 p{
        font-size: 0.7rem;
          color: #eeeeee;
          padding: 1rem 0 2rem 0;
      }
      .footer_center p{
        font-size: 0.7rem;
          color: #eeeeee;
          padding: 1rem 0 2rem 0;
      }
      .footer_right1 p{
        font-size: 0.7rem;
          color: #eeeeee;
          padding: 1rem 0 1rem 0;
      }
      .footer_right2 p {
        font-size: 0.7rem;
        color: #eeeeee;
        padding: 1rem 0 1rem 0;
      }
      .footer_imgs{
        display: flex;
        flex-direction: row;
        justify-content: center;
        flex-wrap: wrap;
        margin: 1rem 0 0 1rem;
    }
    .footer_all_imgs img{
        width: 5rem;
        height: 4rem;
        margin: 1rem 1rem 0 1rem;
    }

}