.team_bg{
    background-image: url('../../images/p51.png');
    background-size: 100% 100%;
    background-repeat: no-repeat;
    width: 100%;
    height: 85vh;
    margin-bottom: 3rem;
}
.team_headings h1{
    font-size: 2rem;
    text-align: center;
    padding: 4rem 0 1rem 0;
    color: #2D2D2D;
}
.team_headings p{
    font-size: 1.2rem;
    text-align: center;
    color: #2D2D2D;
}
.all_team_bg{
    position: relative;
    background-image: url('../../images/p52.png');
    background-size: 100% 100%;
    background-repeat: no-repeat;
    width: 100%;
    height: 60vh;
    margin: 3rem 0 3rem 0;
}
.box_3{
    position: absolute;
    top: 80%;
    left: 40%;
   width: 75%;
    height:30%;
    background: #FFFFFF 0% 0% no-repeat padding-box;
box-shadow: 0px 25px 25px #00000012;
border-radius: 6px;
}
.box_3 h1{
  font-size: 1rem;
  padding: 1rem;
}
/* 2nd section css here */
.our_ceo{
    margin-top: 13rem;
}
.bg_img_1{
    position: relative;
    background-image: url('../../images/p53.png');
    background-size: 100% 100%;
    background-repeat: no-repeat;
    height: 17rem;
   width: 100%;
   float: left;
}
.bg_img_2{
   
    background-image: url('../../images/p54.png');
    background-size: 100% 100%;
    background-repeat: no-repeat;
    height: 17rem;
    width: 100%;
    
}
.bg_white_box{
    position: absolute;
    top: 90%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 23rem;
    height: 6rem;
    background: #FFFFFF 0% 0% no-repeat padding-box;
box-shadow: 0px 25px 25px #0000000F;
opacity: 1;
}
.bg_white_box p{
  font-size: .8rem;
  padding: 1rem;
  color: #2D2D2D;
}
/* 3rd section css */
.our_founder{
    margin-top: 5rem;
}
.bg_img_3{
    position: relative;
    background-image: url('../../images/p55.png');
    background-size: 100% 100%;
    background-repeat: no-repeat;
    height: 17rem;
   width: 100%;
}
.bg_white_box_headung h1{
    font-size: 1.5rem;
    color: #2D2D2D;
}
.bg_white_box_headung p{
    font-size: .9rem;
    color: #2D2D2D;
    padding: 1rem 0 1rem 0;
    line-height: 1.8;
}
/* 4th section css */
.our_founder_grig{
margin-top: 6rem;
}
.big_img_box{
    position: relative;
    background-image: url('../../images/p57.png');
    background-size: 100% 100%;
    background-repeat: no-repeat;
    height: 33rem;
    width: 100%;
}
.box-big {
    position: absolute;
    top: 83%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 80%;
    height: 17%;
    background: #EEEEEE 0% 0% no-repeat padding-box;
opacity: 1;
}
.box-big p{
    font-size: .8rem;
    color: #2D2D2D;
    padding: 1rem;
}
@media screen and (max-width:800px){
    .team_bg{
        height: 80vh;
        margin-bottom: 3rem;
    }
    .team_headings h1{
        font-size: 1.2rem;
        padding: 3rem 1rem 1rem 1rem;
    }
    .team_headings p{
        font-size: 1.1rem;
        padding: 0 1rem 1rem 1rem;
       
    }
    .all_team_bg{
        height: 35vh;
        margin: 2rem 1rem 3rem 2rem;
    }
    .box_3{
        position: absolute;
        top: 70%;
        left: 10%;
       width: 80%;
        height:52%;
    }
    .box_3 h1{
      font-size: .8rem;
      padding: 1rem;
    }
    .our_ceo{
        margin-top: 8rem;
    }
    .bg_white_box{
        top: 72%;
        left: 50%;
        width: 77%;
        height: 28%;
        border-radius: 4px;
    }
    .bg_white_box p{
        font-size: .6rem;
        padding: .5rem;
        color: #2D2D2D;
      }
      .our_founder{
        margin-top: 3rem;
    }
    .bg_white_box_headung h1{
        font-size: 1.2rem;
        padding: 1rem .5rem 1rem 1rem;
    }
    .bg_white_box_headung p{
        font-size: .9rem;
        padding: 0rem .5rem 1rem 1rem;
        line-height: 1.2;
    }
    .our_founder_grig{
        margin-top: 2rem;
        }
    .big_img_box{
        height: 31rem;
    }
    .box-big {
        top: 82%;
        height: 20%;
      
    }
    .box-big p{
        font-size: .7rem;
    }
}